<template>
	<view>
		<wybar @clickLeft="back" :leftText="i18n.toher.xiedianping" :rightText="i18n.mine.submit" :rightcolor="rightColor" @clickRight="submit"></wybar>
		<view class="column" style="margin-top: 10px;width: 100%;background-color: #FFF;">
			<view class="row nameBackView oneline" style="font-size: 18px;font-weight: bold;">
				{{storeName}}
			</view>
			<view class="row xingBack" style="margin-top: 8px;">
				<view class="" style="margin-right: 10px;">
					{{i18n.toher.zongti}}
				</view>
				<starv style="padding-top: 5px;" size = 30 @change="onChange()" :disabled = "false" tag="0" :value="zongtiV"></starv>
			</view>
			
			<view class="row xingBack">
				<view class="" style="margin-right: 10px;">
					{{i18n.meishi.kouwei}}
				</view>
				<starv style="padding-top: 5px;" size = 30 @change="onChange()" :disabled = "false" tag="1" :value="kouweiV"></starv>
			</view>
			<view class="row xingBack">
				<view class="" style="margin-right: 10px;">
					{{i18n.index.fuwu}}
				</view>
				<starv style="padding-top: 5px;" size = 30 @change="onChange()" :disabled = "false" tag="2" :value="fuwuV"></starv>
			</view>
			<view class="column" style="width: calc(100% - 30px);margin-left: 15px;background-color: #f5f6f7;border-radius: 4px;">
				<textarea name="" id="" cols="30" rows="10" :placeholder="i18n.toher.xiepingjiaTips" style="margin-left: 30rpx;margin-top: 20rpx;width: 630rpx;height: 100px;"></textarea>
				<view class="row" style="flex-wrap: wrap;margin-left: 30rpx;width: 630rpx;margin-top: 10px;">
						<view v-for="(item,index) in imgList" :key="index" style="width: 210rpx;height: 210rpx;align-items: center;justify-content: center;display: flex;position: relative;margin-top: 10rpx;margin-bottom: 10rpx;">
							<image :src="item" mode="aspectFill" style="width: 195rpx;height: 195rpx;border-radius: 8rpx;"></image>
							<image @click="delateImage(index)" src="../../../static/images/delate.png" class="" style="position: absolute;right: 0;top: 0;width: 32rpx;height: 32rpx;border-radius: 16rpx;">
								
							</image>
						</view>
						<view class="column" style="align-items: center;justify-content: center;width: 210rpx;height: 210rpx;margin-top: 10rpx;margin-bottom: 10rpx;" v-if="imgList.length < 9" @click="doAddImage()">
							<view class="addBtn column" style="width: 195rpx;height: 195rpx;border-radius: 8rpx;">
								<image src="../../../static/images/picUp-t.png" mode="aspectFit" style="width: 28px;height: 28px;"></image>
								<view class="" style="font-size: 12px;color: #989898;">
									{{i18n.mine.shangchuantupian}}
								</view>
							</view>
				
						</view>
						
					</view>
					
				</view>
			</view>
			
	
	</view>
</template>

<script>
	import wybar from "@/components/wybar/wyBar.vue"
	import starv from "@/components/banxing/banStar.vue"
	export default {
		components: {
			wybar,
			starv
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
				
		},
		onLoad(option) {
			this.storeName = option.name
			
		},
		data() {
			return {
				rightColor:'#AAA',
				imgList:[],
				storeName:'',
				zongtiV:0,
				kouweiV:0,
				fuwuV:0
			}
		},
		methods: {
			back(){
				uni.navigateBack()
			},
			submit(){
				if(this.rightColor == '#7900B2'){
					console.log(12121312)
				}
			},
			delateImage(index){
				this.imgList.splice(index,1)
				this.checkStatus()
			},
			doAddImage(){
				var that = this
				uni.chooseImage({
					count: 9 - that.imgList.length,
					sizeType: ['original', 'compressed'],
					sourceType: ['album', 'camera'],
					success: function(res) {
						let tempFilePaths = res.tempFilePaths;
						for (var i = 0; i < tempFilePaths.length; i++) {
							var path = tempFilePaths[i];
							if(that.imgList.length < 9){
								that.imgList.push(path)
							}
							
						}
						that.checkStatus()
					}
				})
			},
			checkStatus(){
				if(this.imgList.length > 0)	{
					this.rightColor = '#7900B2'
				}else{
					this.rightColor = '#AAA'
				}
			},
			onChange(e){
				if(e.tag == 0){
					this.zongtiV = e.value
				}else if(e.tag == 1){
					this.kouweiV = e.value
				}else{
					this.fuwuV = e.value
				}
				console.log(this.zongtiV,this.kouweiV,this.fuwuV)
			},
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #F5F6F7;
		overflow: hidden;
	}
	.nameBackView {
		width: calc(100% - 30px);
		margin-left: 15px;
		height: 45px;
		line-height: 45px;
		align-items: center;
		border-bottom: 0.5px solid #D6DADE;
	}
	.addBtn {
		align-items: center;
		justify-content: center;
		border: 1px dashed #989898;
	}
	.xingBack {
		width: calc(100% - 30px);
		margin-left: 15px;
		height: 32px;
		line-height: 32px;
		font-size: 14px;
		align-items: center;
	}
</style>
